___CSS_LOADER_EXPORT___.push([module.id, `.display-dropdown {
  border-radius: 10px;
  background: #FFF;
  box-shadow: 0px 10px 60px 0px rgba(42, 102, 193, 0.15);
  width: 560px;
  padding: 40px 45px 0;
  min-height: 690px;
}
.display-dropdown__input {
  margin-bottom: 30px;
}
.display-dropdown__title {
  color: #8B919E;
  font-family: Montserrat;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: 23px; /* 176.923% */
}
.display-dropdown__btn {
  margin-top: 3px;
  border-radius: 20px;
  background: #F2F5FA;
  max-width: 470px;
  width: 100%;
  height: 35px;
  position: relative;
}
.display-dropdown__btn button {
  position: absolute;
  right: 0;
}
.display-dropdown__btn-delete {
  display: flex;
  align-items: center;
  color: #F04973;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  text-decoration-line: underline;
  gap: 5px;
}
.display-dropdown__user {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  border-top: #969696 1px solid;
  border-right: #969696 1px solid;
  border-left: 1px solid #969696;
  padding: 0 19px;
}
.display-dropdown__user:last-child {
  border-bottom: 1px solid #969696;
}
.display-dropdown__list-user {
  color: #454B58;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  padding-top: 5px;
