___CSS_LOADER_EXPORT___.push([module.id, `.lesson-form__info {
  display: flex;
  justify-content: space-between;
}
.lesson-form__buttons {
  display: flex;
  gap: 15px;
  margin: 40px 0 100px;
}
.lesson-form__buttons-big {
  width: 200px;
}
.lesson-form__buttons-small {
  width: 130px;
}
.lesson-form__main {
  border-radius: 10px;
  background: #FFF;
  box-shadow: 0px 10px 60px 0px rgba(42, 102, 193, 0.15);
  max-width: 780px;
  width: 100%;
  padding: 40px 45px 30px;
  max-height: 690px;
}

.form-top__name-title {
  color: #8B919E;
  font-family: Montserrat;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  padding-top: 5px;
  padding-bottom: 8px;
}
.form-top__teacher {
  margin-top: 15px;
}

.form-bottom {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 27px;
}
.form-bottom__decor {
  width: 7px;
  height: 1px;
  background-color: #8B919E;
}
.form-bottom__left {
  padding-top: 15px;
  max-width: 330px;
}
.form-bottom__right {
  max-width: 330px;
  padding-top: 15px;
}
.form-bottom__right-age {
  padding-top: 17px;
}
.form-bottom__right-description {
  padding-top: 17px;
}
.form-bottom__right-description textarea {
  border-radius: 20px;
  background: #F2F5FA;
  width: 328px;
  height: 189px;
  padding: 20px;
}
.form-bottom__right-quantity {
  padding-top: 13px;
}
.form-bottom__time-lesson {
  padding-top: 17px;
}
.form-bottom__time-lesson-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form-bottom__time-lesson-time {
  padding-top: 15px;
}
.form-bottom__time-lesson-duration {
  padding-top: 15px;
}
.form-bottom__time-lesson-price {
  padding-top: 17px;
  position: relative;
}
.form-bottom__time-lesson-price img {
  position: absolute;
  top: 55px;
  right: 15px;
  z-index: 2;
}
.form-bottom__time-lesson-quantity {
  padding-top: 17px;
}`, "",{"version":3,"sources":["webpack://./src/components/pages/LessonForm/LessonForm.scss"],"names":[],"mappings":"AAEI;EACI,aAAA;EACA,8BAAA;AADR;AAII;EACI,aAAA;EACA,SAAA;EACA,oBAAA;AAFR;AAIQ;EACI,YAAA;AAFZ;AAIQ;EACI,YAAA;AAFZ;AAMI;EACI,mBAAA;EACA,gBAAA;EACA,sDAAA;EACA,gBAAA;EACA,WAAA;EACA,uBAAA;EACA,iBAAA;AAJR;;AAUQ;EACI,cAAA;EACA,uBAAA;EACA,eAAA;EACA,kBAAA;EACA,gBAAA;EACA,gBAAA;EACA,mBAAA;AAPZ;AAUI;EACI,gBAAA;AARR;;AAYA;EACI,aAAA;EACA,8BAAA;EACA,cAAA;AATJ;AAUI;EACI,UAAA;EACA,WAAA;EACA,yBAAA;AARR;AAWI;EACI,iBAAA;EACA,gBAAA;AATR;AAWI;EACI,gBAAA;EACA,iBAAA;AATR;AAUQ;EACI,iBAAA;AARZ;AAUQ;EACI,iBAAA;AARZ;AASY;EACI,mBAAA;EACA,mBAAA;EACA,YAAA;EACA,aAAA;EACA,aAAA;AAPhB;AAUQ;EACI,iBAAA;AARZ;AAWI;EACI,iBAAA;AATR;AAUQ;EACI,aAAA;EACA,8BAAA;EACA,mBAAA;AARZ;AAUQ;EACI,iBAAA;AARZ;AAUQ;EACI,iBAAA;AARZ;AAUQ;EACI,iBAAA;EACA,kBAAA;AARZ;AASY;EACI,kBAAA;EACA,SAAA;EACA,WAAA;EACA,UAAA;AAPhB;AAUQ;EACI,iBAAA;AARZ","sourcesContent":[".lesson-form{\n\n    &__info{\n        display: flex;\n        justify-content: space-between;\n    }\n\n    &__buttons{\n        display: flex;\n        gap: 15px;\n        margin: 40px 0 100px;\n\n        &-big{\n            width: 200px;\n        }\n        &-small{\n            width: 130px;\n        }\n    }\n\n    &__main{\n        border-radius: 10px;\n        background: #FFF;\n        box-shadow: 0px 10px 60px 0px rgba(42, 102, 193, 0.15);\n        max-width: 780px;\n        width: 100%;\n        padding: 40px 45px 30px;\n        max-height: 690px;\n\n    }\n}\n.form-top{\n    &__name{\n        &-title{\n            color: #8B919E;\n            font-family: Montserrat;\n            font-size: 13px;\n            font-style: normal;\n            font-weight: 500;\n            padding-top: 5px;\n            padding-bottom:8px;\n        }\n    }\n    &__teacher{\n        margin-top: 15px;\n    }\n}\n\n.form-bottom{\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    grid-gap: 27px;\n    &__decor{\n        width: 7px;\n        height: 1px;\n    